<template>
  <div id="addtask">
     <label ><span>标题</span><input type="text" v-model="info.title" ></label>
     <label ><span>描述</span><input type="text" v-model="info.description" ></label>
     <label ><span>优先级</span><input type="text" v-model="info.priority" ></label>
     <label><span>截止</span><input type="text" v-model="info.deadline" ></label>
     <label><span>已完成</span><input type="checkbox" v-model="info.state" ></label>
     <button @click="addtask">添加</button>
  </div>
</template>

<script>
export default {
  name: 'AddTask',
  data () {
    return {
      info: {
        title: '',
        description: '',
        priority: '',
        deadline: '',
        state: false

      }
    }
  },
  methods: {
    addtask () {
      // 点击添加按钮的时候把数据传递给父组件，父组件拿到数据后加入id然后生成新数据再加入到数组中
      this.$emit('add', this.info)
      // 清空任务添加组件中的输入信息
      this.info = {
        title: '',
        description: '',
        priority: '',
        deadline: '',
        state: false

      }
    }
  }

}
</script>

<style lang="less" scoped>
#addtask{
  display: flex;
  width: 100%;
  // justify-content: center;
  margin: 0 0.267vw;
  height: 6.667vw;
  font-size: 2.133vw;
  label{
    flex: 1;
    input{
      width: 80%;
      margin: 0 auto;
      height: 2.667vw;
      border-radius: 0.667vw;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // border: 1px solid black;
  }
}
</style>
